.hero-text {
  letter-spacing: 0.015em;
}

.hero-text h2 {
  font-weight: 400;
}

.hero-text h3 {
  font-weight: 700;
}

.step-1-card {
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  background: #fff;
  border: 2px solid #e6eef1;
  color: #202c45;
  font-size: 1.4rem;
  max-width: 350px;
  position: relative;
  border-radius: 10px;
  padding: 10px;
  transition: transform 0.2s;
  margin: 10px auto;
}

.step-1-card-active {
  border: 4px solid var(--theme-active);
}

.step-1-card:hover {
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1);
  cursor: pointer;
}

.step-1-card p .p-bold-default {
  font-weight: 900;
}

.step-1-card p .p-bold-active {
  color: var(--theme-active);
}

.step-1-card .circle-default {
  background: #e6eef1;
  border: 1px solid #d5e3e8;
  border-radius: 100%;
  content: "";
  display: block;
  height: 30px;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 30px;
}

.step-1-card .circle-active {
  background: var(--theme-active) url("./checkmark.png") 4px 5px no-repeat;
  border: 1px solid var(--theme-active);
}
