.home-about {
  background-color: #f6f9fa;
}

.home-about a {
  color: #00b1ff;
  text-decoration: none;
  font-style: italic;
}

.article-div .btn-a {
  font-weight: 700;
  letter-spacing: 0.015em;
  font-size: 20px;
  color: var(--theme-secondary);
}

.link-card-div .link-card {
  border: 5px solid var(--theme-secondary-light);
  padding: 10px;
}

.link-card-div h5 {
  margin-bottom: 0px;
  font-weight: 700;
  letter-spacing: 0.015em;
}

.link-card-div ul li {
  margin-bottom: 5px;
}

.link-card-div ul li a {
  text-decoration: none;
}

.home-about img {
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .home-about img {
    width: 100px;
  }
}
