.step-q-button {
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  background-color: var(--theme-secondary);
  border: none;
  border-bottom: 2px solid #cca000;
  border-radius: 6px;
  color: #202c45;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 600;
  text-shadow: 0 1px 1px rgb(255 255 255 / 55%);
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  display: block;
  margin: 0 auto 10px auto;
  max-width: 280px;
  line-height: 1em;
  padding: 15px 10px;
  position: relative;
  text-align: center;
}

.step-q-button:hover {
  background-color: #cca000;
}

.step-q-button-active {
  color: var(--theme-active);
  background-color: #fff;
  border: 2px solid var(--theme-active);
}

.step-back-btn {
  cursor: pointer;
}

.error-message {
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  background-color: #ff0101dc;
  border: none;
  border-bottom: 2px solid #ff0101;
  border-radius: 6px;
  color: #fff;
  font-size: 15px;
  font-weight: 100;
  text-shadow: 0 1px 1px rgb(255 255 255 / 55%);
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  display: block;
  margin: 10px auto 10px auto;
  max-width: 280px;
  line-height: 1em;
  padding: 4px 10px;
  position: relative;
  text-align: center;
}

.input-box-style {
  background: #fff;
  border: 1px solid #595d5d;
  border-radius: 6px;
  font-size: 1.4rem;
  font-weight: bold;
  padding: 0.4em 0.4em;
  width: 100%;
}

.quote-tab-dark .q-step-head-text {
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
}

.animate__animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeInRightBig {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(2000px, 0, 0);
    transform: translate3d(2000px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}
@keyframes fadeInRightBig {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(2000px, 0, 0);
    transform: translate3d(2000px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.animate__fadeInRightBig {
  -webkit-animation-name: fadeInRightBig;
  animation-name: fadeInRightBig;
}

.quote-tab-dark .q-step-head-text {
  color: white;
}

.quote-tab-dark .q-step-sub-text {
  color: rgba(255, 255, 255, 0.664);
}

.quote-tab-light .q-step-head-text {
  color: var(--theme-primary);
  font-weight: 700;
}

.quote-tab-light .q-step-sub-text {
  color: var(--theme-primary);
}

.quote-tab-dark .hero-text {
  color: white;
  text-shadow: 0 2px 5px rgb(0 0 0 / 25%);
}

.quote-tab-light .hero-text {
  color: var(--theme-primary);
}

.quote-tab-dark .step-back-btn {
  color: white;
}

.quote-tab-light .step-back-btn {
  color: var(--theme-primary);
}
