.thank-you-card {
  border: 0 none;
  box-shadow: 0 0 10px rgb(0 0 0 / 40%);
  background-color: white;
  color: #4c5050;
}

.thank-you-card p {
  margin: 0;
  text-align: start;
}

.thank-you-card a {
  text-decoration: none;
}

.thank-you-card .list-div {
  margin-top: 20px;
}

.thank-you-card .star-div {
  text-align: start;
  margin-top: 10px;
}

.thank-you-card img {
  width: 100%;
}

.thank-you-card .btn-a {
  font-weight: 700;
}

@media only screen and (min-width: 600px) {
  .thank-you-card .btn-a {
    float: left;
  }
}
